.app {
  text-align: center;
  font-family: Arial, sans-serif;
}
.search-bar {
  margin: 20px;
  display: flex; 
  flex-wrap: wrap; 
  justify-content: space-between;
}
.header{
  color: rosybrown;
  font-size: 22px;
}
.search input {
  padding: 10px;
  width: 250px;
  height: 35px;
  border-radius: 10px;
}
.search button {
  border-radius: 10px;
  height: 35px;
  margin-left: 10px;
}
.book-list {
  display: flex; 
  flex-wrap: wrap; 
  justify-content: space-between;
  font-size: 15px;
  font-style: italic;
  padding: 10px;
}
.book-container{
  width: 25%;
}
.book-card {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 10px;  
}
.book-card img {
  width: 250px; 
  height: 250px;
  margin-bottom: 8px;
}
.book-details {
  padding: 0;  
}
.book-details h3 {
  margin-top: 0;
}
.book-details p {
  margin: 5px 0;
}
.text{
 font-size: 22px;
 margin-top:250px
}






